 class=\"article-meta\">\r\n            <router-link :to=\"`/profile/${article.author.username}`\" class=\"author-info\">\r\n              <img :src=\"article.author.avatarUrl\" :alt=\"article.author.username\" />\r\n              <div>\r\n                <div class=\"author-name\">{{ article.author.username }}</div>\r\n                <div class=\"publish-date\">{{ formatDate(article.publishedAt) }}</div>\r\n              </div>\r\n            </router-link>\r\n            <div class=\"article-stats\">\r\n              <span><el-icon><View /></el-icon> {{ article.likesCount }}</span>\r\n            </div>\r\n          </div>\r\n        </header>\r\n        \r\n        <div class=\"article-body\">\r\n          <!-- XSS Ê∏≤ÊüìËØ¥ÊòéÔºö\r\n               - VULNÔºöÊñáÁ´†Ê≠£ÊñáÂéüÊ†∑Ê∏≤ÊüìÔºàÂèØÊâßË°åËÑöÊú¨Ôºâ\r\n               - SECUREÔºö‰ΩøÁî® DOMPurify ÂáÄÂåñÔºåÈòªÊñ≠ËÑöÊú¨ÊâßË°å -->\r\n          <div class=\"content\"\r\n               v-if=\"configStore.xssMode === 'vuln'\"\r\n               v-html=\"article.contentHtml\"></div>\r\n          <div class=\"content\"\r\n               v-else\r\n               v-html=\"pure(article.contentHtml)\"></div>\r\n        </div>\r\n        \r\n        <div class=\"article-tags\">\r\n          <span v-for=\"tag in article.tags\" :key=\"tag.id\" class=\"tag\" :style=\"{ background: tag.color + '20', color: tag.color }\">\r\n            {{ tag.name }}\r\n          </span>\r\n        </div>\r\n      </article>\r\n      \r\n      <section class=\"comments-section\">\r\n        <h3>ËØÑËÆ∫ ({{ comments.length }})</h3>\r\n        \r\n        <!-- ËØÑËÆ∫ËæìÂÖ•Ê°ÜÔºöÂè™Âú®ÁôªÂΩïÂêéÊòæÁ§∫ -->\r\n        <div v-if=\"authStore.isAuthenticated\" class=\"comment-form card\">\r\n          <el-form @submit.prevent=\"submitComment\">\r\n            <el-form-item>\r\n              <el-input\r\n                v-model=\"newComment\"\r\n                type=\"textarea\"\r\n                :rows=\"3\"\r\n                placeholder=\"ÂÜô‰∏ã‰Ω†ÁöÑËØÑËÆ∫...\"\r\n                maxlength=\"2000\"\r\n                show-word-limit\r\n              />\r\n            </el-form-item>\r\n            <el-form-item>\r\n              <el-button type=\"primary\" native-type=\"submit\" :loading=\"submitting\" :disabled=\"!newComment.trim()\">\r\n                ÂèëË°®ËØÑËÆ∫\r\n              </el-button>\r\n            </el-form-item>\r\n          </el-form>\r\n          \r\n          <!-- XSS ÊèêÁ§∫ÔºàÂú∫ÊôØ 3 ËØÑËÆ∫Ë†ïËô´ÔºâÔºö‰ªÖÂú® VULN Ê®°ÂºèÂ±ïÁ§∫ÊîªÂáªÁ§∫‰æã -->\r\n          <div class=\"demo-info\" v-if=\"configStore.xssMode === 'vuln'\">\r\n            <h4>‚ö†Ô∏è ËØÑËÆ∫Ë†ïËô´ÊºîÁ§∫ÔºàÂú∫ÊôØ 3Ôºâ</h4>\r\n            <p>ÂΩìÂâçÂ§Ñ‰∫é VULN Ê®°ÂºèÔºåËØÑËÆ∫ÂÜÖÂÆπÂ∞Ü‰∏çÁªèËøáÊª§Áõ¥Êé•Â≠òÂÇ®„ÄÇ</p>\r\n            <p style=\"margin-top: 8px;\">Á§∫‰æã PayloadÔºàËá™‰º†Êí≠Âà∞ 3 ÁØáÊñáÁ´†ÔºâÔºö</p>\r\n            <code style=\"font-size: 0.75rem;\">&lt;img src=x onerror=\"/*ËßÅÊñáÊ°£ÂÆåÊï¥Ë†ïËô´‰ª£Á†Å*/alert('ËØÑËÆ∫ XSS')\"&gt;</code>\r\n            <p style=\"margin-top: 8px; font-size: 0.85em; color: var(--color-text-muted);\">\r\n              üí° ÂÆåÊï¥ Payload ËØ∑Êü•Áúã„ÄäXSSÊºîÁ§∫Âú∫ÊôØËØ¥Êòé.md„Äã\r\n            </p>\r\n          </div>\r\n        </div>\r\n        <div v-else class=\"login-prompt card\">\r\n          <p>ÁôªÂΩïÂêéÊâçËÉΩÂèëË°®ËØÑËÆ∫„ÄÇ <router-link to=\"/login\">Á´ãÂç≥ÁôªÂΩï</router-link></p>\r\n        </div>\r\n        \r\n        <!-- ËØÑËÆ∫ÂàóË°® -->\r\n        <div v-for=\"comment in comments\" :key=\"comment.id\" class=\"comment card\">\r\n          <div class=\"comment-author\">\r\n            <img :src=\"comment.user.avatarUrl\" :alt=\"comment.user.username\" />\r\n            <div>\r\n              <div class=\"comment-author-name\">{{ comment.user.username }}</div>\r\n              <div class=\"comment-date\">{{ formatDate(comment.createdAt) }}</div>\r\n            </div>\r\n          </div>\r\n          <!-- XSS Ê∏≤ÊüìËØ¥ÊòéÔºö\r\n               - VULNÔºöËØÑËÆ∫ÂéüÊ†∑Ê∏≤ÊüìÔºàÂ≠òÂÇ®Âûã XSS Á§∫‰æãÔºâ\r\n               - SECUREÔºöÁ∫ØÊñáÊú¨Ê∏≤Êüì -->\r\n          <div class=\"comment-content\"\r\n               v-if=\"configStore.xssMode === 'vuln'\"\r\n               v-html=\"comment.contentHtml\"></div>\r\n          <div class=\"comment-content\"\r\n               v-else>{{ comment.contentHtml }}</div>\r\n        </div>\r\n      </section>\r\n    </div>\r\n    \r\n    <div v-else-if=\"loading\" class=\"loading\">\r\n      <div class=\"spinner\"></div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\n// ÊñáÁ´†ËØ¶ÊÉÖ‰∏éËØÑËÆ∫ÔºàÂú∫ÊôØ 3ÔºöËØÑËÆ∫Ë†ïËô´Ôºâ\r\nimport { ref, onMounted } from 'vue';\r\nimport { useRoute } from 'vue-router';\r\nimport axios from '@/api/axios';\r\nimport { useConfigStore } from '@/stores/config';\r\nimport { useAuthStore } from '@/stores/auth';\r\nimport { pure } from '@/utils/xss';\r\nimport { ElMessage } from 'element-plus';\r\n\r\nconst route = useRoute();\r\nconst configStore = useConfigStore();\r\nconst authStore = useAuthStore();\r\nconst article = ref(null);\r\nconst comments = ref([]);\r\nconst loading = ref(true);\r\nconst newComment = ref('');\r\nconst submitting = ref(false);\r\n\r\nconst fetchArticle = async () => {\r\n  try {\r\n    const response = await axios.get(`/articles/${route.params.id}`);\r\n    article.value = response.data;\r\n    \r\n    const commentsResponse = await axios.get(`/articles/${route.params.id}/comments`);\r\n    comments.value = commentsResponse.data;\r\n  } catch (error) {\r\n    console.error('Failed to fetch article:', error);\r\n  } finally {\r\n    loading.value = false;\r\n  }\r\n};\r\n\r\nconst submitComment = async () => {\r\n  if (!newComment.value.trim()) return;\r\n  \r\n  submitting.value = true;\r\n  try {\r\n    const response = await axios.post(`/articles/${route.params.id}/comments`, {\r\n      content: newComment.value\r\n    });\r\n    \r\n    // Ê∑ªÂä†Êñ∞ËØÑËÆ∫Âà∞ÂàóË°®È¶ñ‰Ωç\r\n    comments.value.unshift(response.data);\r\n    newComment.value = '';\r\n 